@import url('https://fonts.googleapis.com/css?family=Patrick+Hand+SC|Roboto&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  list-style-type: none;
  text-align: center;
  width: 50%;
  margin: auto;
  padding: 20px;
  background: whitesmoke;
}

.navbar li {
  display: inline-block;
  width: 25%;
  cursor: pointer;
  font-weight: bold;
}
.navbar li span:hover {
  color: #be6eaf;
  border-bottom: 4px solid #3E75A8;
  padding-bottom: 10px;
}

@media only screen and (max-width: 600px) {
  .navbar {
    width: 100%;
  }
  .clothing {
    width: 100%;
  }
}

.clothing {
  width: 75%;
  margin: auto;
  background-color: aliceblue;
  padding: 20px;
}
.clothing li {
  display: inline-block;
  width: 50%;
}

.background {
  background-color: black;
}
.text-white {
  color: white;
}
.wearStyles {
  width: 100%;
}
.carousel .thumbs-wrapper {
  margin-bottom: 0;
}

.text-blue {
  color: #3E75A8 !important;
  font-weight: bold;
}
.text-pink {
  color: #be6eaf !important;
  font-weight: bold;
}
#root {
  overflow-x: hidden;
}
.load-more {
  display: block;
  background: #3E75A8;
  padding: 9px;
  text-decoration: none;
  color: white;
  font-weight: bold;
  margin: 20px 20px;
}
.load-more:hover {
  color: #be6eaf;
  text-decoration: underline;
}
